.app-window{position:absolute !important;z-index:2;opacity:var(--app-window-opacity)}.app-window__layout{height:100%;width:100%;overflow:hidden;border:var(--app-window-layout-border);border-radius:var(--app-window-layout-border-radius)}.app-window__header{padding:var(--app-window-header-padding) !important;line-height:0 !important;display:flex;align-items:center;height:var(--app-window-header-height) !important;background:var(--app-window-header-color) !important}.app-window__content{background:var(--app-window-content-color);padding:var(--app-window-content-padding);overflow-x:hidden;overflow-y:scroll}.app-window__content::-webkit-scrollbar-track{border-radius:0.125rem;background-color:transparent}.app-window__content::-webkit-scrollbar{width:0.5rem;border-radius:0.125rem}.app-window__content::-webkit-scrollbar-thumb{border-radius:0.25rem;background-color:gray}

