.loader{width:100%;height:100%;display:flex;justify-content:center;align-items:center}

.desktop{height:100vh;width:100vw;background:url(/static/media/desktop.9a1e0df1.webp) center center no-repeat !important;background-size:cover !important}.desktop-content{height:100%;padding:var(--desktop-content-padding);overflow:hidden}.desktop-footer{background:var(--desktop-footer-color) !important;height:var(--desktop-footer-height) !important;padding:var(--desktop-footer-padding)}


.widget{position:absolute !important;z-index:2;opacity:var(--widget-opacity)}.widget-layout{overflow:hidden;border:var(--widget-border);border-radius:var(--widget-border-radius);background:var(--widget-background);padding:var(--widget-padding)}

