.clock {
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(./images/clock.webp);
  background-size: cover;
  border: 10px solid #25bcf8;
  border-radius: 50%;
  box-shadow: 0 -5px 5px rgba(255, 255, 255, 0.05),
    inset 0 -5px 5px rgba(255, 255, 255, 0.05), 0 10px 10px rgba(0, 0, 0, 0.3),
    inset 0 10px 10px rgba(0, 0, 0, 0.3);
}
.clock:before {
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  background: #fff;
  border-radius: 50%;
  z-index: 10000;
}
.clock .hour,
.clock .min,
.clock .sec {
  position: absolute;
}
.clock .hour,
.hr {
  width: 100px;
  height: 100px;
}
.clock .min,
.mn {
  width: 130px;
  height: 130px;
}
.clock .sec,
.sc {
  width: 150px;
  height: 150px;
}
.hr,
.mn,
.sc {
  display: flex;
  justify-content: center;
  position: absolute;
  border-radius: 50%;
}
.hr:before {
  content: '';
  position: absolute;
  width: 6px;
  height: 50px;
  background: #ff105e;
  z-index: 10;
  border-radius: 6px 6px 0 0;
}
.mn:before {
  content: '';
  position: absolute;
  width: 4px;
  height: 60px;
  background: #fff;
  z-index: 11;
  border-radius: 6px 6px 0 0;
}
.sc:before {
  content: '';
  position: absolute;
  width: 2px;
  height: 70px;
  background: #ff105e;
  z-index: 12;
  border-radius: 6px 6px 0 0;
}
